import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SessionService } from '@ecp-caf/caf-common';
import { RestfulService } from './service/restful-service/restful.service';
import { SchemaManagerService } from './service/schema-manager/schema-manager.service';
import { LcpService } from './service/lcp-service/lcp.service';
import { ParasMappingManagerService } from './service/paras-mapping/paras-mappingmanager.service';
import { JointSearchManagerService } from './service/joint-search/joint-searchmanager.service';
import { PrintIntegrationManagerService } from './service/print-integration/print-integrationmanager.service';

@NgModule({
  imports: [
    CommonModule,
  ],
  declarations: [
  ],
  exports: [
  ]
})
export class QdpCommonModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: QdpCommonModule,
      providers: [
        SessionService,
        SchemaManagerService,
        RestfulService,
        LcpService,
        ParasMappingManagerService,
        JointSearchManagerService,
        PrintIntegrationManagerService
      ]
    };
  }
}
