type Func = (args: any) => any;

export class EventBus {
  public static handlers: any = {};

  static register(eventType: string, handler: Func, eventId?: string) {
    if (!this.handlers[eventType]) {
      this.handlers[eventType] = {};
    }

    let id = eventId;
    if (!id) {
      id = this.guid();
    } 
    this.handlers[eventType][id] = handler;

    const unsubscribe = () => {
      delete this.handlers[eventType][id];
      if (Object.keys(this.handlers[eventType]).length === 0) {
        delete this.handlers[eventType];
      }
    };

    return unsubscribe;
  }

  static dispatch(eventType: string, args: any) {
    if (this.handlers[eventType]) {
      Object.keys(this.handlers[eventType]).forEach(key => {
        this.handlers[eventType][key](args);
      });
    }
  }

  static guid() {
    function S4() {
      // tslint:disable-next-line:no-bitwise
      return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    }
    return (S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4());
  }
}
