export class JSONUtils {
    static isObj(object) {
        return object && typeof (object) == 'object' && Object.prototype.toString.call(object).toLowerCase() == "[object object]";
    }

    static isArray(object) {
        return object && typeof (object) == 'object' && object.constructor == Array;
    }

    static getLength(object) {
        var count = 0;
        for (var i in object) count++;
        return count;
    }
    static Compare(objA, objB) {
        if (!JSONUtils.isObj(objA) || !JSONUtils.isObj(objB)) return false; //å¤æ­ç±»åæ¯å¦æ­£ç¡®
        if (JSONUtils.getLength(objA) != JSONUtils.getLength(objB)) return false; //å¤æ­é¿åº¦æ¯å¦ä¸è´
        return JSONUtils.CompareObj(objA, objB, true); //é»è®¤ä¸ºtrue
    }

    private static CompareObj(objA, objB, flag) {
        for (var key in objA) {
            if (!flag) //è·³åºæ´ä¸ªå¾ªç¯
                break;
            if (!objB.hasOwnProperty(key)) {
                flag = false;
                break;
            }
            if (!JSONUtils.isArray(objA[key])) { //å­çº§ä¸æ¯æ°ç»æ¶,æ¯è¾å±æ§å¼
                if (objB[key] != objA[key]) {
                    flag = false;
                    break;
                }
            } else {
                if (!JSONUtils.isArray(objB[key])) {
                    flag = false;
                    break;
                }
                var oA = objA[key],
                    oB = objB[key];
                if (oA.length != oB.length) {
                    flag = false;
                    break;
                }
                for (var k in oA) {
                    if (!flag) //è¿éè·³åºå¾ªç¯æ¯ä¸ºäºä¸è®©éå½ç»§ç»­
                        break;
                    flag = JSONUtils.CompareObj(oA[k], oB[k], flag);
                }
            }
        }
        return flag;
    }
}